import React, { Component } from "react";
import { Container, Row, Col, Button } from "react-bootstrap";
import { WebNav } from "../../components/navbar";
import { Footer } from '../../components/footer';
import { BsChevronDoubleDown } from "react-icons/bs";

/** image */
import logo from "../../img/logo.png";
import nckuLogo from "../../img/ncku_logo.png";
import nnImg from "../../img/nn.webp";
import bmiImg from "../../img/bmi.webp";


class Home extends Component {
  render() {
    return (
      <React.StrictMode>
        <WebNav />
        <div className='home'>
          <Cover />
          <Slogan />
          <About />
          <Future />
          <JoinUs />
        </div>
        <Footer />
      </React.StrictMode>
    );
  }
}

export { Home };


function Cover() {
  return (
    <div>
      <div className='cover'>
        <div className='cover-inner'>
          <div className='subtitle'>
            {/* <img src={nckuLogo} style={{height:'3rem'}} /> */}
            <p>NCKU</p>
          </div>
          <div className='title'>
            <p>Neural Computation<br />Laboratory</p>
          </div>
        </div>
        <div className='down'>
          <BsChevronDoubleDown />
        </div>
      </div>
    </div>

  )

}

function Slogan(params) {
  return (

    <div className='section' style={{ backgroundColor: '#5899E2' }}>
      <Container fluid className='h-100 w-100'>
        <Row style={{ height: '15%' }}>
          <Col xs={12} md={3} className='h-100'>
            <div className='subTitle my-5'>
              <p>#</p>
            </div>
          </Col>
        </Row>
        <Row className='' style={{ height: '85%' }}>
          <Col className='h-100'>
            <div className='slogan'>
              <p>我們專門培養資料科學家</p>
              <p>We train students as data scientist</p>
            </div>
          </Col>
        </Row>
      </Container>

    </div>

  )
}


function About(params) {
  return (
    <div className='section' style={{ backgroundColor: '#403E3E' }}>
      <Container fluid className='about'>
        <Row style={{ height: '15%' }}>
          <Col xs={12} md={3} className='h-100'>
            <div className='subTitle my-5' style={{ color: '#F0F0F0' }}>
              <p>Research</p>
            </div>
          </Col>
        </Row>
        <Row className='mt-1'>
          <Col xs={12} md={6}>
            <div className='card'>
              <div className='title'>
                <p>類神經網路<br />Neural Network</p>
              </div>
              <div className='imagebox'>
                <img className='h-100 w-100 p-5' src={nnImg} />
              </div>
            </div>
          </Col>
          <Col xs={12} md={6}>
            <div className='card'>
              <div className='title'>
                <p>侵入式腦機介面<br />Invasive Brain Machine Interface</p>
              </div>
              <div className='imagebox'>
                <img className='h-100 w-100 p-5' src={bmiImg} />
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  )
}

function Future(params) {
  return (
    <div className='section' style={{ backgroundColor: '#F0F0F0' }}>
      <Container fluid className='h-100 w-100'>
        <Row style={{ height: '15%' }}>
          <Col xs={12} md={3} className='h-100'>
            <div className='subTitle my-5' style={{ color: '#403E3E' }}>
              <p>Future</p>
            </div>
          </Col>
        </Row>
        <Row className='' style={{ height: '85%' }}>
          <Col className='h-100'>

          </Col>
        </Row>
      </Container>

    </div>
  )
}

function JoinUs(params) {
  return (
    <div className='section' style={{ backgroundColor: '#FFA400' }}>
      <Container fluid className='h-100 w-100'>
        <Row style={{ height: '15%' }}>
          <Col xs={12} md={3} className='h-100'>
            <div className='subTitle my-5'>
              <p>Join Us</p>
            </div>
          </Col>
        </Row>
        <Row className='' style={{ height: '85%' }}>
          <Col className='h-100'>
            <div>
              <p>與實驗室聯繫</p>
              <Button variant="outline-dark" href='/#/contact'>聯絡資訊</Button>              
            </div>
          </Col>
          <Col className='h-100'>
          <div>
              <p>碩士新生</p><p>面試報名</p>
              <Button variant="outline-dark" target="_blank" href='https://docs.google.com/forms/d/e/1FAIpQLSddVTVVC7N0iLtpVYTZs3vcPknwJzIXBcXCjtT_mhQPcZY-tw/viewform'>前往表單</Button>
              
            </div>
          </Col>
        </Row>
      </Container>

    </div>
  )
}
