import React, { Component, useState, useEffect } from "react";
import { WebNav } from "../../components/navbar";
import { CoverHalf } from "../../components/coverHalf";
import { Footer } from '../../components/footer';
import Loader from 'react-loader-spinner'
import { Container, Row, Col } from "react-bootstrap";
import photoData from "./data";


function Photo(params) {
  const [csvData, setCsvData] = useState([]);
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      // const url =
      //   "";
      // readRemoteFile(url, {
      //   complete: (results) => {
      //     // console.log("Results:", results);
      //     setCsvData(results.data);
      //     setGetData(true);
      //   },
      // });
      setCsvData(photoData);
      setGetData(true);
    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf pageName='Photo' />
        <PhotoSection data={csvData} />
        <Footer />
      </React.StrictMode>
    )
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf pageName='Photo' />
        <div className='text-center my-5'>
          <Loader type="Grid" color="#403E3E" height={80} width={80} />
        </div>
        <Footer />
      </React.StrictMode>
    )
  }
}



export { Photo };

function PhotoSection(props) {
  let data = props.data
  return (
    <Container>
      <Row>
        {
          data.map(d => (
            <Col xs={12} md={6}>
              <PhotoCard src={d.image} text={d.text} />
            </Col>

          ))
        }
      </Row>
    </Container>
  )
}

// 16:9
function PhotoCard(props) {
  return (
    <div>
      <div
        style={{
          width: "100%",
          paddingBottom: "56.25%",
          height: "0",
          position: "relative",
        }}
      >
        <div
          style={{
            position: "absolute",
            top: "0",
            bottom: "0",
            right: "0",
            left: "0",
          }}
        >
          <div
            style={{
              height: "100%",
              overflow: "hidden",
              display: "flex",
              justifyContent: "center",
              backgroundColor:'#f0f0f0'
            }}
          >
            <img style={{ height: "100%" }} src={props.src} />
          </div>
        </div>
      </div>
      <div>
        <p className='text-center'>{props.text}</p>
      </div>
    </div>

  )
}